form {
  display: inline-block;
  margin-right: 15px;
}

.btn {
  background-color: royalblue;
  color: white;
  padding: 10px 15px;
  font-size: 18px;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  transition: all .2s;
}

.btn:hover {
  opacity: 0.9;
}

.btn:active {
  transform: scale(0.98);
}