.title {
  text-align: center;
  font-size: 40px;
  letter-spacing: -1px;
  margin: 80px 0;
}

.loading {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contents {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.imgBox {
  width: 25%;
}

.imgBox img {
  width: 100%;
}

.textBox {
  width: 60%;
  margin-left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.textBox p {
  font-size: 20px;
}

.textBox ul li {
  font-size: 18px;
}

.textBox span {
  font-weight: 600;
  font-size: 24px;
}

.textBox .info {
  width: 60%;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width:1280px) {
  .textBox p {
    font-size: 16px;
  }

  .textBox ul li {
    font-size: 14px;
  }

  .textBox span {
    font-size: 20px;
  }
}

@media screen and (max-width:1024px) {
  .title {
    font-size: 28px;
    margin: 40px 0;
  }

  .imgBox {
    width: 100%;
    text-align: center;
  }

  .imgBox img {
    width: 50%;
  }

  .textBox {
    width: 100%;
    text-align: center;
    padding: 0 30px;
    margin-top: 20px;
    margin-left: 0;
    align-items: center;
  }

  .textBox p {
    font-size: 16px;
  }

  .textBox ul li {
    font-size: 16px;
    margin-bottom: 5px;
  }

  .textBox span {
    display: inline-block;
    font-size: 20px;
    margin: 15px 0;
  }

  .textBox .info {
    width: 95%;
    display: flex;
    justify-content: center;
  }

  .textBox .info p {
    margin-right: 20px;
  }

  .buttonWrap {
    margin: 20px 0;
  }

  .buttonWrap button {
    margin: 5px 0;
  }
}