.movieList {
  width: 48%;
  min-height: 300px;
  display: flex;
  margin-bottom: 50px;
  cursor: pointer;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  transition: .2s;
}

.movieList:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.imgBox {
  width: 30%;
}

.imgBox img {
  width: 100%;
  height: 100%;
  min-height: 300px;
}

.textBox {
  width: 70%;
  padding-left: 20px;
}

.textBox h3 {
  margin-top: 5px;
  font-size: 26px;
}

.textBox p {
  margin: 20px 0;
}

.textBox ul li {
  font-size: 14px;
}

@media screen and (max-width:1280px) {
  .movieList {
    width: 100%;
    min-height: 0;
  }

  .imgBox {
    width: 20%;
  }

  .imgBox img {
    min-height: 0;
  }

  .textBox {
    width: 80%;
    padding-left: 20px;
  }

  .textBox h3 {
    font-size: 22px;
  }

  .textBox p {
    font-size: 15px;
    margin: 15px 0;
  }

  .textBox ul li {
    font-size: 13px;
  }
}

@media screen and (max-width:767px) {
  .imgBox {
    min-width: 100px;
  }

  .textBox p {
    display: none;
  }

  .textBox ul {
    margin-top: 10px;
  }
}